import React, { Component } from 'react';
import PageLink from '../PageLink';
import MenuIcon from './MenuIcon';
import { connect } from 'react-redux';
import './SideMenu.scss';

export class SideMenu extends Component {
     constructor(props){
         super(props);

     }

     render(){
         return <div className={this.props.menuState}>
                     <ul>
                        <li className="list-item"><MenuIcon linkIcon="icon-basic-folder-multiple"/><PageLink linkPath="/projects" linkName="Projects"/></li>
                        <li className="list-item"><MenuIcon linkIcon="icon-basic-notebook"/><PageLink linkPath="/education" linkName="Education"/></li>
                        <li className="list-item"><MenuIcon linkIcon="icon-basic-notebook-pen"/><PageLink linkPath="/microblog" linkName="Microblog"/></li>
                        <li className="list-item"><MenuIcon linkIcon="icon-basic-folder-multiple"/><a className="nav-link" href="https://medium.com/@patrickackerman">Medium</a></li>
                        <li className="list-item"><MenuIcon linkIcon="icon-basic-paperplane"/><PageLink linkPath="/contact" linkName="Contact"/></li>
                     </ul>
                </div>
     }
}

function mapStateToProps(state){
    return {
        menuState: state.menu.menuState
    }
}

export default connect(mapStateToProps)(SideMenu);



