import * as types from '../actions/actionTypes';

export default function menuReducer(state={menuState: 'side-menu-IN'}, action){
    switch(action.type){
        case types.SET_MENU_STATE:
            let ret = Object.assign({}, state, {menuState: 'side-menu-' + action.className})
            console.log('SET_MENU_STATE', ret);
            return ret;
            break;
        default:
            return state;
    }
}



